.search-container {
  position: relative;
  width: 80%;
  max-width: 600px;
  margin: 50px auto;
  background: rgba(255, 255, 255, 0.8); 
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.search-input {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.suggestions {
  width: 100%;
  list-style: none;
  border: 1px solid #ccc;
  border-top: none;
  max-height: 200px;
  overflow-y: auto;
  padding: 5px;
  margin: 0;
  background: white;
  position: relative;
  width: 100%;
  z-index: 1;
  color: black;
}

.suggestions li {
  padding: 10px;
  cursor: pointer;
  background-color: #fff;
}

.suggestions li:hover {
  background-color: #f0f0f0;
}

.suggestions li strong {
  color: #007bff;
}
